
.navbar {
    background: #090909;
    height: 80px;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    
    
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
  
    margin-left: 20px;
    cursor: pointer;
    margin-top: 4rem;
  }
  

  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    
    width: 60vw;
    justify-content: end;

    align-items: center;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #F5F5F5;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #FC4F00;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #FC4F00;
  }

  .fa-times {
    color:#f5f5f5;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: none;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      
      opacity: 1;
      transition: all 0.5s ease;
      
    }
  
    .nav-menu.active {
      display: flex;
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      margin-top: -100px;
    }
  
    .nav-links:hover {
      background-color: #FC4F00;
      color: #f5f5f5;
      border-radius: 8px;
      
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
     height: 100%;
     margin-top: 1rem;
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
     
    }
  
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }
  @media screen and (max-width: 768px) {
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
       height: 100%;
       margin-top: 1rem;
      }
  }