 @import url('https://fonts.googleapis.com/css2?family=Candal&family=Konkhmer+Sleokchher&family=Poppins&display=swap');


body {
  font-family: 'Konkhmer Sleokchher', cursive;
    background-color: rgba(252, 252, 252, 0.937);
  }
  
  * {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
   
    
  
  }
  
  ::-webkit-scrollbar {
    width: 0px;
  }
  
  .right-arrow,
  .left-arrow {
    cursor: pointer;
    background: transparent;
    outline: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FC4F00;
    font-size: 25px;
    border-radius: 4px;
    position: absolute;
    bottom: -20px;
    right: 80px;
    transform: scale(1, 1);
    transition: 0.3s all ease-in-out;
  }
  
  .right-arrow {
    right: 140px;
  }
  
  .right-arrow:hover,
  .left-arrow:hover {
    transform: scale(1.3, 1.3);
  }
  
  .react-horizontal-scrolling-menu--wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  
  
  .detail-image {
    width: 500px;
    height: 500px;
    margin-left: 40px;
    margin-top: 150px;
  }
  


  .hero-banner-vid {
    position: absolute;
    right: 40px;
    top: 0px;
    width: 700px;
    margin-top: 10px;
  
  }
  
  .exercise-card {
    width: 300px;
    height: 345px;
    background: #fff;
    border-top: 4px solid #FC4F00;
    border-bottom-left-radius: 20px;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-bottom: 10px;
    transform: scale(1, 1);
    transition: 0.1s all ease-in-out;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }
  
  .exercise-card img {
    height: 200px;
  }
  
  .bodyPart-card {
    transform: scale(1, 1);
    transition: 0.1s all ease-in-out;
  }
  
  .exercise-card:hover,
  .bodyPart-card:hover,
  .exercise-video:hover
   {
    transform: scale(1.1, 1.1);
  }
  
  .search-btn:hover {
    color: #FC4F00 !important;
    border: 1px solid #FC4F00 !important;
  }
  
  .exercise-video {
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 387px;
    height: 330px;
    text-decoration: none;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 12px;
    transform: scale(1, 1);
    transition: 0.1s all ease-in-out;
    background-color: white;

  }

 
  
  @media screen and (max-width:1200px) {
    .detail-image {
      width: 300px;
      height: 300px;
    }
  
  
    .react-horizontal-scrolling-menu--scroll-container {
      width: 500px;
    }
  
    .left-arrow,
    .right-arrow {
      position: static !important;
    }
  
    .hero-banner-img {
      display: none;
    }

    .hero-banner-vid {
      display: none;
    }
  
    .exercise-card {
      width: 320px;
    }
  
    .exercise-video {
      width: 320px;
      height: 300px;
    }
  
  }
  
  @media screen and (max-width:400px) {
    .exercise-card {
      width: 280px;
    }
  }

  