:root {
    --primary: #fff;
  }
  
  .btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
   
  }
  .btn1{
   
      padding: 8px 20px;
      border-radius: 2px;
      outline: none;
      border: none;
      cursor: pointer;
      font-weight: bold;
      
   
  }

  
  .btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }

  .btn--outline1 {
    background-color: transparent;
    color: #121619;
    padding: 8px 20px ;
    
    transition: all 0.3s ease-out;
    
    
  }
  
  .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: #FC4F00;
    border: 1px solid #FC4F00;
    color: #fff;
    transition: 250ms;
  }
  